import PropTypes from 'prop-types';
import React from 'react';
import Gallery from 'react-grid-gallery';
import axios from "axios";
import ReactDOM from "react-dom";

const imageUrl = 'https://leumi-hanuka.herokuapp.com/public/';
// const imageUrl = 'http://localhost:5000/public/';

class ImageGallery extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            images: this.props.images.map(image => {
                let src = (imageUrl + 'files/' + image.name);
                let width = 1;
                let height = 1;
                if (image.width / image.height > 1.5) {
                    width = 2;
                } else if (image.height / image.width > 1.5) {
                    height = 2;
                }
                return {
                    src: src,
                    thumbnail: src,
                    thumbnailWidth: width,
                    thumbnailHeight: height
                }
            })
        };
    }

    render() {
        return (
            <div>
                <center><img src={`${imageUrl}Title.png`} style={{textAlign: "center"}}/></center>
                <div style={{
                    display: "block",
                    minHeight: "1px",
                    width: "1547px",
                    height: "1819px",
                    overflow: "auto",
                    margin: "auto",
                    backgroundImage: `url(${imageUrl}Candle.png)`,
                    backgroundSize: "cover"
                }}>
                    <div style={{
                        display: "block",
                        minHeight: "1px",
                        width: "600px",
                        height: "1350px",
                        border: "1px solid #ddd",
                        overflow: "auto",
                        margin: "auto",
                        marginTop: "300px"
                    }}>
                        <Gallery
                            images={this.state.images}
                            enableLightbox={true}
                            enableImageSelection={false}/>
                    </div>
                </div>
            </div>
        );
    }
}

ImageGallery.propTypes = {
    images: PropTypes.arrayOf(
        PropTypes.shape({
            src: PropTypes.string.isRequired,
            thumbnail: PropTypes.string.isRequired,
            srcset: PropTypes.array,
            caption: PropTypes.string,
            thumbnailWidth: PropTypes.number.isRequired,
            thumbnailHeight: PropTypes.number.isRequired
        })
    ).isRequired
};

export default (ImageGallery);