import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import Gallery from 'react-photo-gallery';

const imageUrl = 'https://leumi-hanuka.herokuapp.com/public/files/';
// const imageUrl = 'http://localhost:5000/public/files/';

const styles = theme => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
        backgroundColor: theme.palette.background.paper,
    },
    gridList: {
        width: 600,
        height: 500,
    },
    subheader: {
        width: '100%',
    },
});

function ImageGridList(props) {
    const { classes } = props;

    return (
        <div className={classes.root}>
            <GridList cellHeight={300} className={classes.gridList} cols={3}>
                {props.imageNames.map(tile => (
                    <GridListTile key={tile} cols={tile.cols || 1}>
                        <img src={imageUrl + tile} alt={tile.title || "title"} />
                    </GridListTile>
                ))}
            </GridList>
        </div>
    );
}

ImageGridList.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ImageGridList);