import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import SignIn from './sign-in.js';
import ImageGallery from './image-gallery.js'
import UploadPhoto from './upload-photo'
import {createMuiTheme, MuiThemeProvider} from '@material-ui/core/styles';

const theme = createMuiTheme({
    palette: {
        primary: {
            // light: will be calculated from palette.primary.main,
            main: '#004BA6',
            // dark: will be calculated from palette.primary.main,
            // contrastText: will be calculated to contrast with palette.primary.main
        },
        secondary: {
            light: '#0066ff',
            main: '#0044ff',
            // dark: will be calculated from palette.secondary.main,
            contrastText: '#ffcc00',
        },
        // error: will use the default color
    },
});

class App extends React.Component {
    // uploadPhoto = <UploadPhoto onChangeComponent={() => this.setState({component: <ImageGallery/>})}/>;
    signInComponent = <SignIn onChangeComponent={() => this.setState({component: <UploadPhoto/>})}/>;

    state = {
        data: null,
        component: this.signInComponent
    };

    componentDidMount() {
        document.title = "Leumi Hanukkah"
        // Call our fetch function below once the component mounts
        this.callBackendAPI()
            .then(res => this.setState({data: res.express}))
            .catch(err => console.log(err));
    }

    // Fetches our GET route from the Express server. (Note the route we are fetching matches the GET route from server.js
    callBackendAPI = async () => {
        const response = await fetch('/express_backend');
        const body = await response.json();

        if (response.status !== 200) {
            throw Error(body.message)
        }
        return body;
    };

    render() {
        return (
            <MuiThemeProvider theme={theme}>
                {this.state.component}
            </MuiThemeProvider>
        );
    }
}

// ========================================

ReactDOM.render(
    <App/>,
    document.getElementById('root')
);
